import { Stock, NewStockData } from './types';

// Generate a unique ID
const generateId = (): string => {
  return Math.random().toString(36).substr(2, 9);
};

// API function to research a stock
export const researchStock = async (stockData: NewStockData): Promise<Stock> => {
  const params = new URLSearchParams({
    text: `${stockData.stockName} stock`,
    exchange: stockData.exchangeName,
    num_of_articles: stockData.articlesCount.toString()
  });

  const response = await fetch(`http://localhost:8000/research?${params}`);
  
  if (!response.ok) {
    throw new Error(`API request failed: ${response.statusText}`);
  }

  const data = await response.json();
  
  // Add the id and isResearching fields that the API doesn't return
  return {
    ...data,
    id: generateId(),
    isResearching: false
  };
};

// API function to research existing stock
export const researchExistingStock = async (stockId: string, articlesCount: number, stockName: string, exchangeName: string): Promise<Partial<Stock>> => {
  const params = new URLSearchParams({
    text: `${stockName} stock`,
    exchange: exchangeName,
    num_of_articles: articlesCount.toString()
  });

  const response = await fetch(`http://localhost:8000/research?${params}`);
  
  if (!response.ok) {
    throw new Error(`API request failed: ${response.statusText}`);
  }

  const data = await response.json();
  
  // Return the updated data
  return {
    stockName: data.stockName,
    exchangeName: data.exchangeName,
    articlesCount: parseInt(data.articlesCount),
    sentiment: data.sentiment,
    sentimentScore: data.sentimentScore,
    lastResearched: data.lastResearched,
    isResearching: false
  };
};

